import { t } from '../locale';

export default {
  name: 'PressActTipDialog',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: t('dialog.title'),
    },
    message: {
      type: String,
      default: t('actTipDialog.prompt'),
    },
    showCancelButton: {
      type: Boolean,
      default: true,
    },
    showConfirmButton: {
      type: Boolean,
      default: true,
    },
    cancelButtonText: {
      type: String,
      default: t('cancel'),
    },
    confirmButtonText: {
      type: String,
      default: t('confirm'),
    },
  },
  emits: ['confirm', 'cancel'],
  data() {
    return {
    };
  },
  mounted() {
  },
  methods: {
    confirm() {
      this.$emit('confirm', this.show);
    },

    cancel() {
      this.$emit('cancel', this.show);
    },
  },
};

